import React from "react";
import "./Footer.scss";
import { NavLink } from "react-router-dom";
import { menu } from "../../data";

const Footer = () => {
  return (
    <div className="footer">
      <div className="container text-right">
        <div className="row">
          <div className="col footer-left">
            <div className="footer-title">THNG TIN LIN H</div>
            <div className="footer-text">
              <p>
                Knh mt thi trang Eyemax Store l thng hiu uy tn, cht
                lng ti Vit Nam phc v nhu cu mua sm online v trc tuyn
                trn ton quc
              </p>
              <span>
                <h5>a ch: </h5>
                <p>Tn Thnh, Thnh ph Thi Nguyn, Thi Nguyn</p>
              </span>
              <span>
                <h5>S in thoi: </h5>
                <p>0966245700</p>
              </span>
              <span>
                <h5>Email: </h5>
                <p>eyemaxstore@gmail.com</p>
              </span>
            </div>
          </div>
          <div className="col footer-center">
            <div className="footer-title">NHN TIN KHUYN MI</div>
            <div className="footer-text">
              <p>
                ng k nhn bn tin ca chng ti  nhn tin tc s kin mi
                t chng ti qua a ch email ca bn
              </p>
            </div>
            <form className="d-flex">
              <input
                className="form-control me-2"
                placeholder="a ch email....."
              />
              <button className="btn footer-btn" type="submit">
                NG K
              </button>
            </form>
          </div>
          <div className="col footer-right">
            <div className="footer-title">H TR KHCH HNG</div>
            <ul className="footer-list">
              {menu.map((item, index) => (
                <li className="nav-item" key={index}>
                  <NavLink className="nav-link" to={item.link}>
                    {item.name}
                  </NavLink>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Footer;
