import axios from "axios";
import React, { useEffect, useState } from "react";

const SelectAddress = () => {
  const [provinces, setProvinces] = useState([]);
  const [selectedProvince, setSelectedProvince] = useState("");
  const [districts, setDistricts] = useState([]);
  const [selectedDistrict, setSelectedDistrict] = useState("");
  const [wards, setWards] = useState([]);

  useEffect(() => {
    // Gi API  ly danh sch cc tnh thnh
    axios
      .get("https://provinces.open-api.vn/api/p")
      .then((response) => {
        setProvinces(response.data);
      })
      .catch((error) => {
        console.error("Li khi tm np tnh:", error);
      });

    if (selectedProvince !== "") {
      // Gi API  ly danh sch cc qun huyn ca tnh  chn
      axios
        .get(`https://provinces.open-api.vn/api/p/${selectedProvince}?depth=2`)
        .then((response) => {
          setDistricts(response.data.districts);
        })
        .catch((error) => {
          console.error("Li khi tm np qun:", error);
        });
    }

    if (selectedDistrict !== "") {
      // Gi API  ly danh sch cc x phng ca qun huyn  chn
      axios
        .get(`https://provinces.open-api.vn/api/d/${selectedDistrict}?depth=2`)
        .then((response) => {
          setWards(response.data.wards);
        })
        .catch((error) => {
          console.error("Li khi tm np phng:", error);
        });
    }
  }, [selectedProvince, selectedDistrict]);

  const handleProvinceChange = (event) => {
    setSelectedProvince(event.target.value);
    setSelectedDistrict("");
  };

  const handleDistrictChange = (event) => {
    setSelectedDistrict(event.target.value);
  };

  return (
    <>
      <div className="col-md-12">
        <select
          value={selectedProvince}
          onChange={handleProvinceChange}
          className="form-control"
        >
          <option value="">Tnh/Thnh</option>
          {provinces.map((province) => (
            <option key={province.code} value={province.code}>
              {province.name}
            </option>
          ))}
        </select>
      </div>
      <div className="col-md-12">
        <select
          value={selectedDistrict}
          onChange={handleDistrictChange}
          className="form-control"
        >
          <option value="">Chn Qun/Huyn</option>
          {districts.map((district) => (
            <option key={district.code} value={district.code}>
              {district.name}
            </option>
          ))}
        </select>
      </div>
      <div className="col-md-12">
        <select className="form-control">
          <option value="">Chn Phng/X</option>
          {wards.map((ward) => (
            <option key={ward.code} value={ward.code}>
              {ward.name}
            </option>
          ))}
        </select>
      </div>
    </>
  );
};

export default SelectAddress;
