import React, { useContext, useState } from "react";
import "./CheckOut.scss";
import Breadcrumb from "../../components/Breadcrumb/Breadcrumb";
import { BsGeoAlt, BsCart3, BsFront } from "react-icons/bs";
import { useSelector } from "react-redux";
import { AuthContext } from "../../context/authContext";
import SelectAddress from "../../components/SelectAddress";

const item = [
  {
    name: "Trang ch",
    link: "/",
  },
  {
    name: "Thanh ton",
    link: "/thanh-toan",
  },
];

const CheckOut = () => {
  const products = useSelector((state) => state.cart.products);
  const { currentUser } = useContext(AuthContext);
  const [HoVaTen, setHoVaTen] = useState(currentUser.HoVaTen);
  const [copiedText, setCopiedText] = useState("");
  const [Email, setEmail] = useState(currentUser.Email);
  const [SoDienThoai, setSoDienThoai] = useState(currentUser.SoDienThoai);

  const handleCopyClick = async () => {
    const textToCopy = [HoVaTen, SoDienThoai];
    try {
      await navigator.clipboard.writeText(textToCopy);
      setCopiedText(textToCopy);
      console.log(" sao chp vn bn vo khay nh tm");
    } catch (error) {
      console.error("Li sao chp vo khay nh tm:", error);
    }
  };

  const totalPrice = () => {
    let total = 0;
    products.forEach((item) => {
      total += item.Gia * item.SoLuong;
    });
    return total;
  };

  return (
    <>
      <Breadcrumb item={item} />
      <div className="checkout">
        <div className="checkout-title">
          <h2>Thanh ton</h2>
        </div>
        <div className="row">
          <div className="col-7">
            <div className="checkout-title-chilren">
              <BsGeoAlt className="checkout-icon" />
              <h3>THNG TIN KHCH HNG</h3>
            </div>
            <div className="checkout-form">
              <div className="row">
                <div className="col">
                  <div className="checkout-item">
                    <div className="checkout-item-title">
                      <h3>NGI MUA HNG</h3>
                    </div>
                    <div className="col-md-12">
                      <input
                        type="text"
                        className="form-control"
                        placeholder="H v tn"
                        value={HoVaTen}
                        onChange={(e) => setHoVaTen(e.target.value)}
                      />
                    </div>
                    <div className="col-md-12">
                      <input
                        type="email"
                        className="form-control"
                        placeholder="Email"
                        value={Email}
                        onChange={(e) => setEmail(e.target.value)}
                      />
                    </div>
                    <div className="col-md-12">
                      <input
                        type="text"
                        className="form-control"
                        placeholder="in thoi"
                        value={SoDienThoai}
                        onChange={(e) => setSoDienThoai(e.target.value)}
                      />
                    </div>
                  </div>
                </div>
                <div className="col">
                  <div className="checkout-item">
                    <div className="checkout-item-title">
                      <h3>NGI NHN HNG</h3>
                    </div>
                    <div className="col-md-12">
                      <input
                        type="text"
                        className="form-control"
                        placeholder="H v tn"
                        value={copiedText[0]}
                      />
                    </div>
                    <div className="col-md-12">
                      <input
                        type="text"
                        className="form-control"
                        placeholder="in thoi"
                        value={copiedText[1]}
                      />
                    </div>
                    <button onClick={handleCopyClick}>
                      <BsFront />
                      S dng thng tin ngi mua hng
                    </button>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col">
                  <div className="checkout-item">
                    <div className="checkout-item-title">
                      <h3>A CH NHN HNG</h3>
                    </div>
                    <div className="col-md-12">
                      <input
                        type="text"
                        className="form-control"
                        placeholder="a ch"
                      />
                    </div>
                    <SelectAddress />
                  </div>
                </div>
                <div className="col">
                  <div className="checkout-item">
                    <div className="checkout-item-title">
                      <h3>HNH THC GIAO HNG</h3>
                    </div>
                    <div className="checkout-payment">
                      <input type="radio" id="cod" />
                      <label htmlFor="cod">
                        <img src="../../../img/method_bg_cod.png" alt="" />
                        Thanh ton khi nhn hng
                      </label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-5">
            <div className="checkout-title-chilren">
              <BsCart3 className="checkout-icon" />
              <h3>THNG TIN N HNG</h3>
            </div>
            <div className="checkout-form">
              <div className="payment-list">
                {products.map((item) => (
                  <PaymentItem item={item} key={item.MaSanPham} />
                ))}
              </div>
              <div className="box-price">
                <div className="box-price-item">
                  <span>Tm tnh</span>
                  <span>
                    {new Intl.NumberFormat("vi-VN", {
                      style: "currency",
                      currency: "VND",
                    }).format(totalPrice())}
                  </span>
                </div>
                <div className="box-price-item">
                  <span>Ph vn chuyn</span>
                  <span>
                    {new Intl.NumberFormat("vi-VN", {
                      style: "currency",
                      currency: "VND",
                    }).format(30000)}
                  </span>
                </div>
                <div className="box-price-item">
                  <span>Thnh tin</span>
                  <span>
                    {new Intl.NumberFormat("vi-VN", {
                      style: "currency",
                      currency: "VND",
                    }).format(totalPrice() + 30000)}
                  </span>
                </div>
              </div>
              <div className="mb-12">
                <textarea
                  className="form-control"
                  placeholder="Ghi ch"
                  rows="2"
                ></textarea>
              </div>
              <div className="mb-12">
                <button className="box-button">Thanh ton</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default CheckOut;

const PaymentItem = ({ item }) => {
  const PF = "http://localhost:8800/upload/";
  return (
    <div className="payment-product-item">
      <div className="payment-product-image">
        <img src={PF + item?.HinhAnh1} alt="" />
      </div>
      <div className="payment-product-details">
        <div className="col-8 payment-product-name">
          <h3>{item.TenSanPham}</h3>
        </div>
        <div className="col">
          <div className="payment-product-quantity">
            <span>{item.SoLuong}</span>
            <p>x</p>
          </div>
          <div className="payment-product-price">
            <span>
              {new Intl.NumberFormat("vi-VN", {
                style: "currency",
                currency: "VND",
              }).format(item.Gia)}
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};
