import React, { useContext, useEffect } from "react";
import "./InfoPersonal.scss";
import Breadcrumb from "../../components/Breadcrumb/Breadcrumb";
import {
  BsFileEarmarkFill,
  BsPencilSquare,
  BsKeyFill,
  BsFileEarmarkText,
  BsClock,
  BsFillPersonFill,
  BsPhone,
  BsEyeFill,
} from "react-icons/bs";
import { useState } from "react";
import { Link } from "react-router-dom";
import { makeRequest } from "../../axios";
import { AuthContext } from "../../context/authContext";
import moment from "moment";

const item = [
  {
    name: "Trang ch",
    link: "/",
  },
  {
    name: "Thng tin c nhn",
    link: "/thong-tin-ca-nhan",
  },
];

const InfoPersonal = () => {
  const [toggle, setToggle] = useState(1);
  const [data, setData] = useState([]);
  const { currentUser } = useContext(AuthContext);

  useEffect(() => {
    const fetchAllData = async () => {
      try {
        const res = await makeRequest.get(
          `/nguoidung/${currentUser.MaNguoiDung}`
        );
        setData(res.data);
      } catch (err) {
        console.log(err);
      }
    };
    fetchAllData();
  }, [currentUser.MaNguoiDung]);

  function updateToggle(id) {
    setToggle(id);
  }
  return (
    <>
      <Breadcrumb item={item} />
      <div className="info-personal">
        <div className="row">
          <div className="col-3">
            <div className="info-personal-list">
              <div className="info-personal-item disabled">
                QUN L GIAO DCH
              </div>
              <div
                className={
                  toggle === 1
                    ? "info-personal-item active"
                    : "info-personal-item"
                }
                onClick={() => updateToggle(1)}
              >
                <BsFileEarmarkFill />
                n hng ca bn
              </div>
              <div className="info-personal-item disabled">
                QUN L TI KHON
              </div>
              <div
                className={
                  toggle === 2
                    ? "info-personal-item active"
                    : "info-personal-item"
                }
                onClick={() => updateToggle(2)}
              >
                <BsPencilSquare />
                Thng tin ti khon
              </div>
              <div
                className={
                  toggle === 3
                    ? "info-personal-item active"
                    : "info-personal-item"
                }
                onClick={() => updateToggle(3)}
              >
                <BsKeyFill />
                i mt khu
              </div>
            </div>
          </div>
          <div className="col-9">
            <div className={toggle === 1 ? "show-order-tab" : "order-tab"}>
              <OrderTab />
            </div>
            <div className={toggle === 2 ? "show-order-tab" : "order-tab"}>
              <PersonalTab data={data} />
            </div>
            <div className={toggle === 3 ? "show-order-tab" : "order-tab"}>
              <ChangePasswordTab />
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default InfoPersonal;

const OrderTab = () => {
  const [data, setData] = useState([]);
  const { currentUser } = useContext(AuthContext);

  useEffect(() => {
    const fetchAllData = async () => {
      try {
        const res = await makeRequest.get(`/donhang/nguoidung/2}`);
        setData(res.data);
      } catch (err) {
        console.log(err);
      }
    };
    fetchAllData();
  }, [currentUser.MaNguoiDung]);

  return (
    <>
      <div className="order-tab-title">
        <h4>Qun l giao dch</h4>
      </div>

      {data.length > 0
        ? data.map((data) => (
            <div className="order-tab-item">
              <div className="order-tab-item-top row">
                <div className="col-8">
                  <div className="code-order">
                    <BsFileEarmarkText />
                    <span>{data.MaDonHang}</span>
                  </div>
                  <div className="time-order">
                    <BsClock />
                    <span>
                      {" "}
                      {moment(data.NgayTao).format("HH:mm DD/MM/YYYY")}
                    </span>
                  </div>
                </div>
                <div className="col-3">
                  <div className="total-amount">
                    <span>Tng tin:</span>
                    <span>
                      {new Intl.NumberFormat("vi-VN", {
                        style: "currency",
                        currency: "VND",
                      }).format(data.TongTien)}
                    </span>
                  </div>
                </div>
              </div>
              <div className="order-tab-item-bottom row">
                <div className="col-8">
                  <div className="personal-item">
                    <BsFillPersonFill />
                    <span>{data.TenNguoiNhan}</span>
                  </div>

                  <div className="phone-item">
                    <BsPhone />
                    <span>{data.SDTNguoiNhan}</span>
                  </div>
                </div>
                <div className="col-3">
                  <Link
                    to={`/chi-tiet-don-hang/${data.MaDonHang}`}
                    className="order-button links"
                  >
                    <BsEyeFill />
                    Xem chi tit
                  </Link>
                </div>
              </div>
            </div>
          ))
        : "Khng c thng tin cho loi d liu ny"}
    </>
  );
};

const PersonalTab = ({ data }) => {
  return (
    <>
      <div className="order-tab-title">
        <h4>Thng tin ti khon</h4>
      </div>
      <div className="row">
        <div className="col-3">Tn truy cp:</div>
        <div className="col-md-9 contact-form">
          <input
            type="text"
            className="form-control"
            placeholder="Tn truy cp"
            value={data.TenTruyCap}
          />
        </div>
      </div>

      <div className="row">
        <div className="col-3">Email:</div>
        <div className="col-md-9 contact-form">
          <input
            type="email"
            className="form-control"
            placeholder="Email"
            value={data.Email}
          />
        </div>
      </div>

      <div className="row">
        <div className="col-3">H v tn:</div>
        <div className="col-md-9 contact-form">
          <input
            type="text"
            className="form-control"
            placeholder="H v tn"
            value={data.HoVaTen}
          />
        </div>
      </div>

      <div className="row">
        <div className="col-3">in thoi:</div>
        <div className="col-md-9 contact-form">
          <input
            type="text"
            className="form-control"
            placeholder="in thoi"
            value={data.SoDienThoai}
          />
        </div>
      </div>

      <div className="row">
        <div className="col-3">a ch:</div>
        <div className="col-md-9 contact-form">
          <input
            type="text"
            className="form-control"
            placeholder="a ch"
            value={data.DiaChi}
          />
        </div>
      </div>

      <button className="tab-button">Lu thng tin</button>
    </>
  );
};

const ChangePasswordTab = () => {
  return (
    <>
      <div className="order-tab-title">
        <h4>i mt khu</h4>
      </div>
      <div className="row">
        <div className="col-3">Mt khu c:</div>
        <div className="col-md-9 contact-form">
          <input
            type="password"
            className="form-control"
            placeholder="******"
          />
        </div>
      </div>

      <div className="row">
        <div className="col-3">Mt khu mi:</div>
        <div className="col-md-9 contact-form">
          <input
            type="password"
            className="form-control"
            placeholder="******"
          />
        </div>
      </div>

      <div className="row">
        <div className="col-3">Nhp li mt khu:</div>
        <div className="col-md-9 contact-form">
          <input
            type="password"
            className="form-control"
            placeholder="******"
          />
        </div>
      </div>

      <button className="tab-button">i mt khu</button>
    </>
  );
};
